import React from 'react';

const Timer=(props)=>{
    let style={
        border:'1px solid black',
        height:'30px',
        width:'60px', 
        textAlign:'center',
        marginLeft:'40px',
        borderRadius:'5px',
        padding:'5px'
    }
    return(
        <div>
           <p style={style} id={props.id}>00:0{props.time}</p>
        </div>
    )
}

export default Timer;